import React from 'react'
import git from "./git.png"
import discord from "./discord.webp"

export default function Contacts() {
    return (
    <footer className="contacts">
        <a href="https://github.com/FrankR-7" target="_blank" rel='noopener noreferrer'>
            <img src={git} alt="Github Logo"/>
            <p>See my other projects on GitHub!</p>
        </a>
        <a href="https://discord.com/users/411915557625921537" target="_blank" rel='noopener noreferrer'>
        <img src={discord} alt="Discord Logo"/>
            <p>Contact me Through Discord!</p>
        </a>
    </footer>
    )
}
